* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: Black Ops One;
}

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    width: 100%;
    background: rgb(52, 38, 175);
}

ul {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
}

li {
    padding: 10px;
    margin-right: 10px;
    font-size: 1.2rem;
    color: white;
    letter-spacing: 1px;
}

h1 {
    color: white;
}

h2 {
    font-size: 2rem;
    letter-spacing: 2px;
}

span {
    color:rgb(31, 30, 30);
    font-size: 2rem;
}

form {
    width: 100%;
    padding: 20px;
}

.input {
    width: 250px;
    height: 35px;
    outline: none;
    margin-top: 10px;
    margin-right: 15px;
    padding-left: 5px;
}

.btn {
    width: 75px;
    height: 35px;
    border: none;
    outline: none;
    margin-top: 15px;
    background: rgb(31, 30, 30);
    color: rgb(52, 38, 175);
    transition: 0.3s ease;
}

.btn:hover {
    color: white;
    cursor: pointer;
}

::placeholder {
    color: rgb(52, 38, 175);
    font-size: 0.85rem;
}

table {
    zoom: 0.89;
}


th {
    margin-right: 20px;
    padding-right: 15px;
    font-size: 1.1rem;
    color: grey;
    letter-spacing: 1px;
    
}

td {
    margin-right: 20px;
    padding-right: 15px;
    font-size: 1.1rem;
    font-family: Fugaz One;  
}

.outputs {
    padding-left: 20px;
}

.displaynone {
    display: none;
}

.optionsummary {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-evenly;
    position: fixed;
    top: 0;
    height: 100vh;
    width: 500px;
    background: rgb(31, 30, 30);
    transform: translateX(-110%);
    transition: ease 0.4s;
}

.tradingsummary {
    transition: ease 0.3s;
    color: white;
}

.tradingsummary:hover {
    background: white;
    color: rgb(52, 38, 175);
    cursor: pointer;
}

.transIn {
    transform: translateX(0%);
}

.numtrades {
    padding-left: 75px;
    font-size: 1.2rem;
    font-family: Fugaz One;
}

p {
    border-top: 1px solid black;
    width: 500px;
    padding: 10px;
}

p:last-child {
    border-bottom: 1px solid black;
}

.delete {
    display: inline-block;
    font-size: 1.3rem;
    color: rgb(52, 38, 175);
    transition: 0.3s ease;
}

.delete:hover {
    transform: scale(1.3);
    cursor: pointer;
}

.table-responsive {
    min-height: .01%;
    overflow-x: auto;
}

@media screen and (max-width: 767px) {
    .outputs {
        width: 100%;
        margin-bottom: 15px;
        overflow-y: hidden;
        -ms-overflow-style: -ms-autohiding-scrollbar;
        border: 1px solid #ddd;
    }
    .outputs > .table {
        margin-bottom: 0;
    }
    .outputs > .table > thead > tr > th,
    .outputs > .table > tbody > tr > th,
    .outputs > .table > tfoot > tr > th,
    .outputs > .table > thead > tr > td,
    .outputs > .table > tbody > tr > td,
    .outputs > .table > tfoot > tr > td {
        white-space: nowrap;
    }
}